"use client";

import { useState } from "react";
import { MapPin, X } from "lucide-react";
import { DateIcon, ShieldIcon } from "@/assets/svg/svg_icon";
import { PiDotsThreeOutlineVertical } from "react-icons/pi";
import MoreOptionsModal from "../modal/more_option.modal";

interface ChatUser {
    id: number;
    name: string;
    avatar: string;
    avatarColor: string;
    lastMessage: string;
    timestamp: string;
    hasImage?: boolean;
}

interface Message {
    id: number;
    type: "product" | "text" | "negotiation";
    content?: string;
    productName?: string;
    productPrice?: string;
    productImage?: boolean;
    originalPrice?: string;
    newPrice?: string;
    status?: string;
}

const ChatPage = () => {
    const [selectedChat, setSelectedChat] = useState<number>(2);
    const [showMoreOptions, setShowMoreOptions] = useState<boolean>(false);

    const chatUsers: ChatUser[] = [
        {
            id: 1,
            name: "CashNCarry",
            avatar: "CC",
            avatarColor: "bg-blue-200 text-blue-700 border-blue-600",
            lastMessage: "Women vintage sweat bla...",
            timestamp: "2 days ago",
        },
        {
            id: 2,
            name: "Offizy Stores",
            avatar: "OS",
            avatarColor: "bg-purple-200 text-purple-700 border-purple-600",
            lastMessage: "Beachwear light top black...",
            timestamp: "2 days ago",
            hasImage: true,
        },
        {
            id: 3,
            name: "Wearables",
            avatar: "W",
            avatarColor: "bg-green-200 text-green-700 border-green-600",
            lastMessage: "Vintage Jean suede black...",
            timestamp: "2 days ago",
            hasImage: true,
        },
    ];

    const messages: Message[] = [
        {
            id: 1,
            type: "product",
            productName: "Beachwear light top black men's shirt",
            productPrice: "$20.50",
            productImage: true,
        },
        {
            id: 2,
            type: "text",
            content: "Hello, welcome to Offizy Stores\nWhat would you want us to do for you, today?",
        },
        {
            id: 3,
            type: "negotiation",
            originalPrice: "$20.50",
            newPrice: "$15.00",
            status: "Negotiation accepted",
        },
    ];

    return (
        <div className="flex">
            {/* Sidebar - Chat List */}
            <div className="w-80 bg-white border border-gray-200 rounded-l-[18px] overflow-hidden">
                <div className="p-6 border-b border-gray-200">
                    <h1 className="text-2xl font-semibold text-gray-900">Chat</h1>
                </div>

                <div className="overflow-y-auto">
                    {chatUsers.map((user) => (
                        <div
                            key={user.id}
                            onClick={() => setSelectedChat(user.id)}
                            className={`flex items-center gap-3 p-4 border-b border-gray-100 cursor-pointer hover:bg-gray-50 transition-colors ${selectedChat === user.id ? "bg-gray-50" : ""
                                }`}
                        >
                            <div
                                className={`w-12 h-12 rounded-full border ${user.avatarColor} flex items-center justify-center font-semibold text-lg flex-shrink-0`}
                            >
                                {user.avatar}
                            </div>
                            <div className="flex-1 min-w-0">
                                <div className="flex items-center justify-between">
                                    <h3 className="font-semibold text-gray-900 truncate">
                                        {user.name}
                                    </h3>
                                    <span className="text-xs text-gray-500 flex-shrink-0 ml-2">
                                        {user.timestamp}
                                    </span>
                                </div>
                                <p className="text-sm text-gray-700 truncate">
                                    {user.lastMessage}
                                </p>
                                <div className="flex items-center gap-2">
                                    {user.hasImage && (
                                        <div className="w-8 h-8 bg-gray-200 rounded flex-shrink-0"></div>
                                    )}

                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* Main Chat Area */}
            <div className="flex-1 flex flex-col rounded-r-[18px] bg-white border border-l-0 border-gray-200 overflow-hidden">
                {/* Chat Header */}
                <div className="bg-white border-b border-gray-200 p-4 flex items-center justify-between">
                    <div className="flex items-center gap-3">
                        <div className="w-12 h-12 rounded-full bg-purple-200 text-purple-700 border border-purple-600 flex items-center justify-center font-semibold text-lg">
                            OS
                        </div>
                        <div>
                            <h2 className="font-semibold text-gray-900">Offizy Stores</h2>
                            <div className="flex items-center gap-1 text-xs text-gray-500">
                                <div className="w-2 h-2 bg-gray-400 rounded-full"></div>
                                <span>Offline (Last active - 2mins ago)</span>
                            </div>
                        </div>
                    </div>
                    <button
                        onClick={() => setShowMoreOptions(true)}
                        className="cursor-pointer">
                        <PiDotsThreeOutlineVertical className="w-5 h-5 text-gray-600" />
                    </button>
                </div>

                {/* Messages Area */}
                <div className="flex-1 overflow-y-auto space-y-4">
                    {messages.map((message) => (
                        <div key={message.id}>
                            {message.type === "product" && (
                                <div className="flex items-center gap-3 border-b border-gray-100 w-full pb-4 p-4">
                                    <div className="w-12 h-12 bg-gray-200 rounded-lg flex-shrink-0"></div>
                                    <div>
                                        <p className="text-sm font-medium text-gray-900 mb-0.5">
                                            {message.productName}
                                        </p>
                                        <p className="text-sm font-semibold text-gray-900">
                                            {message.productPrice}
                                        </p>
                                    </div>
                                </div>
                            )}

                            {message.type === "text" && (
                                <div className="max-w-md border border-gray-100 rounded-[18px] p-4 m-4">
                                    <div className="mb-1">
                                        <p className="text-normal text-gray-900 whitespace-pre-line">
                                            {message.content}
                                        </p>
                                    </div>
                                    <div className="flex flex-col gap-3 text-xs text-gray-600">
                                        <div className="flex items-center gap-1">
                                            <MapPin className="w-4 h-4 text-black" />
                                            <span>Abuja, Nigeria</span>
                                        </div>
                                        <div className="flex items-center gap-1">
                                            <DateIcon height={15} width={15} />
                                            <span>Last active - 2mins ago</span>
                                        </div>
                                    </div>
                                </div>
                            )}

                            {message.type === "negotiation" && (
                                <div className="flex justify-end p-4">
                                    <div className="bg-[#DDFFF4] border border-[#6CF5CA] rounded-2xl rounded-[10px] p-2 max-w-xs">
                                        <div className="flex items-center gap-3 mb-1">
                                            <span className="text-lg font-medium text-[#077D58]">
                                                {message.newPrice}
                                            </span>
                                            <span className="text-sm text-gray-500 line-through">
                                                {message.originalPrice}
                                            </span>
                                        </div>
                                        <p className="text-xs text-[#077D58] font-medium">
                                            {message.status}
                                        </p>
                                    </div>
                                </div>
                            )}
                        </div>
                    ))}

                    {/* Proceed To Buy Button */}
                    <div className="p-4">
                        <button
                            type="button"
                            className="group w-full max-w-[200px] p-0.5 border border-[#003625] rounded-[18px] cursor-pointer disabled:opacity-50 disabled:cursor-not-allowed">
                            <div className="w-full bg-[#003625] text-white py-4 px-4 rounded-[14px] text-center font-semibold transition-colors duration-200 group-hover:bg-emerald-100 group-hover:text-[#052214]">
                                Proceed To Buy
                            </div>
                        </button>
                    </div>
                </div>

                {/* Safety Notice */}
                <div className="bg-[#F6FAF9] border-t border-gray-200 p-4">
                    <div className="flex items-center justify-between max-w-4xl mx-auto">
                        <div className="flex items-center gap-2">
                            <ShieldIcon />
                            <p className="text-[#4B5563]">
                                Stay safe on FLEZTA. Don't share personal data, click on external links, or scan QR codes.
                            </p>
                        </div>
                        <button className="p-1 hover:bg-gray-200 rounded transition-colors">
                            <X className="w-5 h-5 text-gray-600" />
                        </button>
                    </div>
                </div>
            </div>

            <MoreOptionsModal
                isOpen={showMoreOptions}
                onClose={() => setShowMoreOptions(false)}
            />
        </div>
    );
};

export default ChatPage;